import React, { Component,createRef } from 'react'

import { Card, Input, Button,DatePicker,Spin,message,Row,Col} from 'antd';
import { Form,Icon} from '@ant-design/compatible';
import '@ant-design/compatible/assets/index.css';

import E from 'wangeditor'
import moment from 'moment'
import  './capsule.less'

import {getArticleById,saveArticle,uploadCapsule} from '../../../../services'

const formItemLayout = {
    labelCol:{
        span:3
    },
    wrapperCol:{
        span:18
    }
}

//    this.props.form
//    Form.create()        
//      

// @Form.create()   
//   export default const Edit = Form.create()(Edit);

//    this.props.form    antd Form   


@Form.create()
class UploadCapsule extends Component {
    constructor(){
        super()
        //   ref  DOM
        this.editorRef=createRef()
        this.state={
            isLoading:false,
            dataSource:{}
        }
    }
    handleSubmit = (e)=>{
        e.preventDefault()
        this.setState({
            isLoading:true
        })
        this.props.form.validateFields((err, values) => {
            if (!err) {
              console.log('Received values of form: ', values);
              const newTime = values.capsule_time.valueOf()
              const data = Object.assign({},values,{
                  capsule_time:String(newTime),
              })
              uploadCapsule(data)
              .then(resp =>{
                if(resp.data===1){
                    message.success('')
                    this.props.history.push('/admin/info/capsule')
                }
              })
              .catch(()=>{
                  message.error('')
              })
              .finally(()=>{
                this.setState({
                    isLoading:false
                })
            })
            }
          });
    }
    initEditor = () => {
        this.editor = new E(this.editorRef.current)
        //  this.editorRef  DOM  current 
        this.editor.customConfig.onchange = (html) => {  
            //   wangeditor     html  html 
            this.props.form.setFieldsValue({
                //   setFieldsValue,     {getFieldDecorator('content',....     'content' 
                capsule_content: html
            })
        }
        this.editor.create()
    }
    fillFormData = (source)=>{
        if(source){
            const {capsule_id, ...data} = source
            data.capsule_time = moment(data.capsule_time)
            // console.log(data)
            this.props.form.setFieldsValue(data)
            this.editor.txt.html(data.capsule_content)
            this.setState({
                isLoading:false
            })
        }
    }
    componentDidMount(){
        this.initEditor()
        this.setState({
            isLoading:true,
        })
        this.fillFormData(this.props.dataSource)
        
        // getArticleById(this.props.match.params.id)
        // .then(resp =>{
        //     const {id, ...data} = resp
        //     data.capsule_time = moment(data.capsule_time)
        //     this.props.form.setFieldsValue(data)
        //     this.editor.txt.html(data.content)
        // })
        // .finally(()=>{
        //     this.setState({
        //         isLoading:false
        //     })
        // })
        
    }
    onClickBack=()=>{
        this.props.history.push('/admin/info/capsule')
    }
    render() {
        const{
            getFieldDecorator
        }=this.props.form
        // this.props.form   antd Form       
        //   Form 
        return (
            
                <Card  
                    title={""}
                    bordered={false} 
                    extra={<Button onClick={this.onClickBack}>  </Button>} 
                >
                    <Form 
                        onSubmit={this.handleSubmit} 
                        className="login-form"
                        {...formItemLayout}
                    >
                        <Form.Item
                            label=""
                        >
                        {getFieldDecorator('capsule_title', {
                            rules: [{ required: true, message: '' }],
                        })(
                            <Input
                                placeholder=""
                            />,
                        )}
                        
                        </Form.Item>
                        <Form.Item
                            label=""
                        >
                        {getFieldDecorator('capsule_time', {
                            rules: [
                                { required: true, message: '' },
                            ],
                        })(
                            <DatePicker showTime placeholder="Select Time"  />
                        )}
                        </Form.Item>
                        <Form.Item
                            label=""
                        >
                        {getFieldDecorator('capsule_content', {
                            rules: [
                                { required: true, message: '' },
                            ],
                        })(
                            //   DOM
                            //    ref  DOM <div ref={this.editorRef}> </div> this.editorRef
                        <div className='my-editor' ref={this.editorRef} >
                            
                        </div>
                        )}

                        </Form.Item>
                        <Row>
                            <Col span={2} offset={2}>
                                <Button   type="primary" htmlType="submit" className="login-form-button">
                                    
                                </Button>
                            </Col>
                        </Row>
                    </Form>
                </Card>
        )
    }
}
export default UploadCapsule
